import  Axios  from "axios";
import { CART_EMPTY } from "../constants/cartConstants";
import { ORDER_CREATE_REQUEST, 
    ORDER_CREATE_FAIL, 
    ORDER_CREATE_SUCCESS,
    ORDER_DETAILS_REQUEST,
    ORDER_DETAILS_FAIL,
    ORDER_DETAILS_SUCCESS,
    ORDER_PAY_REQUEST,
    ORDER_PAY_FAIL,
    ORDER_PAY_SUCCESS,
    ORDER_MINE_LIST_REQUEST,
    ORDER_MINE_LIST_FAIL,
    ORDER_MINE_LIST_SUCCESS,
    ORDER_LIST_REQUEST,
    ORDER_LIST_FAIL,
    ORDER_LIST_SUCCESS,
    ORDER_DELETE_REQUEST,
    ORDER_DELETE_FAIL,
    ORDER_DELETE_SUCCESS,
    ORDER_DELIVER_REQUEST,
    ORDER_DELIVER_SUCCESS,
    ORDER_DELIVER_FAIL,} from '../constants/orderConstants';

export const createOrder = (order) => async (dispatch, getState) => {
    dispatch({type: ORDER_CREATE_REQUEST, payload: order});
    try{
        /*Importing userInfo from userSignIn*/
        const {userSignin: { userInfo }} = getState();
        /*AJAX Request*/
        const { data } = await Axios.post('api/orders', order, {
            headers: {
                Authorization: `Bearer ${userInfo.token}`,
            },
        });
        dispatch({ type: ORDER_CREATE_SUCCESS, payload: data.order});
        dispatch({type: CART_EMPTY});
        localStorage.removeItem('cartItems');

    } catch (error) {
        dispatch({
            type: ORDER_CREATE_FAIL,
            payload:
                error.response && error.response.data.message
                ? error.response.data.message
                : error.message,
    });
  }
};

/*Defining detailsOrder from the orderScreen --> useEffect*/

export const detailsOrder = (orderId) => async(dispatch, getState) => {
    dispatch({ type: ORDER_DETAILS_REQUEST, payload: orderId });
    
    /*Geeting functional of getState and importing userInfo data*/
    const {userSignin : {userInfo}} = getState();
    
    try{ /*AJAX request*/
        const { data } = await Axios.get(`/api/orders/${orderId}`, {
            headers: { Authorization: `Bearer ${userInfo.token}`}
        });                                    /*userInfo is obtained from getState*/
        dispatch({ type: ORDER_DETAILS_SUCCESS, payload: data});
    } catch (error) {
        const message = error.message && error.response.data.message
        ? error.response.data.message
        : error.message;
        dispatch({ type: ORDER_DETAILS_FAIL, payload: message});
    }
};


/*For defining successfullPaymentHandler in the ORderScreen*/
export const payOrder = (order, paymentResult) => async(dispatch, getState) => {
    dispatch ({type: ORDER_PAY_REQUEST, payload: {order, paymentResult } });
    const {userSignin : {userInfo}} = getState();

        /*AJAX request*/
        try{ /*Calling pay API and its type is PUT for payment order details in Paypal*/
            const { data } = Axios.put(`/api/orders/${order._id}/pay`, paymentResult, {           
            headers: { Authorization: `Bearer ${userInfo.token}`},
        });
         dispatch({type: ORDER_PAY_SUCCESS, payload: data});
        } catch (error) {
            const message = error.message && error.response.data.message
            ? error.response.data.message
            : error.message;
            dispatch({ type: ORDER_PAY_FAIL, payload: message});
        }
    } 

export const listOrderMine = () => async(dispatch, getState) => {
    dispatch({type: ORDER_MINE_LIST_REQUEST});
    const { userSignin: {userInfo}} = getState();
    try{
        /*AJAX REQUEST*/
        const { data} = await Axios.get('/api/orders/mine', {
            /*Authorizzation for getting TOKEN*/
            headers: {
                Authorization: `Bearer ${userInfo.token}`,
            },
        });
        dispatch({ type: ORDER_MINE_LIST_SUCCESS, payload: data});
    } catch (error) {
        const message = 
        error.response && error.response.data.message
        ? error.response.data.message
        : error.message;
        dispatch({type: ORDER_MINE_LIST_FAIL, payload: message});

    }
};

/*For viewing order history by admins. "listOrders" is a fx in OrderListScreen.js*/
export const listOrders = () => async(dispatch, getState) => {
    dispatch({ type: ORDER_LIST_REQUEST});
    const {userSignin: {userInfo}} = getState();
    try{
        const { data } = await Axios.get('/api/orders', {
            headers: {Authorization: `Bearer ${userInfo.token}`}
        });
        dispatch({ type: ORDER_LIST_SUCCESS, payload: data});
    } catch (error) {
        const message = error.message && error.response.data.message
        ? error.response.data.message
        : error.message;
        dispatch({ type: ORDER_LIST_FAIL, payload: message});
    }
};

/*For deleting orders by ADMIN. "deleteOrder" fx from OrderListScreen.js is defining here*/
export const deleteOrder = (orderId) => async (dispatch, getState) => {
    dispatch({ type: ORDER_DELETE_REQUEST, payload: orderId});
    const {userSignin:{userInfo}} = getState();
    try{
        const { data } = await Axios.delete(`/api/orders/${orderId}`, {
            headers: { Authorization: `Bearer ${userInfo.token}`},
        });
        dispatch({ type: ORDER_DELETE_SUCCESS, payload: data });
    } catch (error) {
        const message = error.message && error.response.data.message
        ? error.response.data.message 
        : error.message;
        dispatch({ type: ORDER_DELETE_FAIL, payload: message});
    }
};

